@use 'base/variables' as v;
@use 'base/mixins' as m;

.proceso-cafe {
    margin-bottom: v.$separacion;

    @include m.tablet {
        display: flex;
        gap: v.$separacion;
        align-items: center;

        &:nth-child(even) {
            flex-direction: row-reverse;
        }
    }

    &:last-of-type {
        margin-bottom: 0;
    }

    .imagen,
    .texto {
        flex: 1;
    }

    .texto {
        margin-top: 2rem;

        h3 {
            margin: 0;
            text-align: left;
            color: v.$primario;

            &::after {
                content: "";
                display: block;
                width: 50%;
                height: 5px;
                background-color: v.$secundario;
            }
        }
    }
}